import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBIcon,
  MDBCardImage
} from 'mdb-react-ui-kit';
import UserIcon from '../images/04-Dragana-Petronijevic.jpg';
import { Helmet } from 'react-helmet-async';
import { NavLink } from 'react-router-dom';

export const IndexPage = () => {
  return (
    <section style={{ backgroundColor: '#eee' }}>
      <Helmet>
        <title>Dragana Petronijevi - Sudski tuma za engleski jezik</title>

        <meta name="description" content="Dragana Petronijevi - Iskusan sudski tuma i prevodilac za engleski jezik sa vie od 30 godina iskustva, specijalizovana za ovjerene i neovjerene prevode, kao i simultano i konsekutivno prevoenje." />
        <meta name="keywords" content="dragana petronijevic, prevodilac, sudski tuma, tuma, engleski jezik, crnogorski jezik, prevoenje, ovjereni prevodi, neovjereni prevodi, prevodilac Herceg Novi, prevodilac Podgorica, prevodilac Crna Gora, sudski tuma Herceg Novi, sudski tuma Podgorica, sudski tuma Crna Gora, prevodi, prevodioci, simultano prevoenje, konsekutivno prevoenje, struni prevodi" />
      </Helmet>
      <MDBContainer className="py-5">
        <MDBRow className="justify-content-center align-items-center text-center mb-5" style={{ height: '40vh' }}>
          <MDBCol lg="8">
            <h1>Profesionalne prevodilake usluge</h1>
            <p className="lead text-muted">
            Kao sudski tuma za engleski jezik sa dugogodinjim iskustvom, prevodim i ovjeravam sve vrste tekstova i dokumenata
            </p>

            <div className="d-flex justify-content-center">
                <NavLink to="/kontakt" className="btn btn-outline-primary">Kontaktirajte me</NavLink>
            </div>
          </MDBCol>
        </MDBRow>

        <MDBRow className="text-center mb-5">
          <MDBCol md="4">
            <MDBCard>
              <MDBCardBody>
                <MDBIcon fas icon="globe" size="3x" className="mb-3" />
                <MDBCardTitle>Usluge prevoenja</MDBCardTitle>
                <MDBCardText>
                Brze i precizne usluge prevoenja svih vrsta tekstova, dokumenata i audio materijala
                </MDBCardText>
                <div className="d-flex justify-content-center">
                <NavLink to="/usluge-prevodjenja" className="btn btn-outline-primary">Saznajte vie</NavLink>
                </div>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
          <MDBCol md="4">
            <MDBCard>
              <MDBCardBody>
                <MDBIcon fas icon="coins" size="3x" className="mb-3" />
                <MDBCardTitle>Cjenovnik</MDBCardTitle>
                <MDBCardText>
                Pristupane cijene i fleksibilni paketi za irok spektar prevodilakih usluga  
                </MDBCardText>
                <div className="d-flex justify-content-center">
                <NavLink to="/cjenovnik" className="btn btn-outline-primary">Saznajte vie</NavLink>
                </div>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
          <MDBCol md="4">
            <MDBCard>
              <MDBCardBody>
              <MDBCardImage
                src={UserIcon}
                alt="Dragana Petronijevi"
                className="mb-2 rounded-circle"
                style={{ width: '55px' }}
              />
                <MDBCardTitle>O meni</MDBCardTitle>
                <MDBCardText>
                Iskusan prevodilac i sudski tuma za engleski jezik sa vie od 30 godina iskustva
                </MDBCardText>
                <div className="d-flex justify-content-center">
                <NavLink to="/o-meni" className="btn btn-outline-primary">Saznajte vie</NavLink>
                </div>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </section>
  );
};
