import {
    MDBCol,
    MDBContainer,
    MDBRow,
    MDBCard,
    MDBCardText,
    MDBCardBody,
    MDBCardImage,
    MDBListGroup,
    MDBListGroupItem
  } from 'mdb-react-ui-kit';
import UserIcon from '../images/04-Dragana-Petronijevic.jpg';
import { Helmet } from 'react-helmet-async';


export const AboutMePage = () => {
    return (
      <section style={{ backgroundColor: '#eee' }}>
        <Helmet>
          <title>O meni - Dragana Petronijevi</title>

          <meta name="description" content="Saznajte vie o Dragani Petronijevi, sudskom tumau i prevodiocu za engleski jezik sa vie od 30 godina iskustva, specijalizovana za strune prevode, ovjerene i neovjerene prevode, simultano i konsekutivno prevoenje." />
          <meta name="keywords" content="dragana petronijevic, sudski tuma, prevodilac, o meni, iskustvo, prevoenje, ovjereni prevodi, neovjereni prevodi, engleski jezik, crnogorski jezik, strucni prevodi, simultano prevoenje, konsekutivno prevoenje" />
      </Helmet>
        <MDBContainer className="py-5">
          <MDBRow>
            <MDBCol lg="12">
              <MDBCard className="mb-4">
                <MDBCardBody className="d-flex align-items-center">
                  <MDBCardImage
                    src={UserIcon}
                    alt="Dragana Petronijevi"
                    className="rounded-circle"
                    style={{ width: '150px', marginRight: '20px' }}
                    fluid
                  />
                  <div>
                    <h4 className="text-center"><b>O meni</b></h4>
                    <MDBCardText className="text-muted">
                      <b>Zovem se Dragana Petronijevi.</b> Prevodilac sam i sudski tuma za engleski jezik ve vie od tri decenije. Diplomirala sam na Filolokom fakultetu Univerziteta u Sarajevu, gdje sam stekla struni naziv profesora engleskog jezika i knjievnosti.
                    </MDBCardText>
                  </div>
                </MDBCardBody>
              </MDBCard>
            </MDBCol>
  
            <MDBCol lg="12">
              <MDBCard className="mb-4">
                <MDBCardBody>
                  <MDBRow>
                    <MDBCol sm="12" md={{ size: 9, offset: 1 }} className="mb-4">
                      <MDBCardText className="text-muted">
                        <b>Licenca za sudskog tumaa za engleski jezik</b> dodijeljena mi je od strane Ministarstva pravde Crne Gore 1991. godine, od kada sam kontinuirano obnavljala svoju licencu i unapreivala svoje vjetine, kako kroz rad sa klijentima, tako i kroz dodatnu obuku.
                      </MDBCardText>
                    </MDBCol>
                  </MDBRow>
  
                  <MDBRow>
                    <MDBCol sm="12" md={{ size: 9, offset: 1 }} className="mb-4">
                      <h5 className="text-center"><b>Institucije i organizacije</b></h5>
                      <MDBListGroup>
                        <MDBListGroupItem className="text-muted">
                        lanica sam <b>Udruenja sudskih tumaa Crne Gore</b> i <b>Udruenja konferencijskih prevodilaca Crne Gore (AMIC)</b>.
                        </MDBListGroupItem>
                        <MDBListGroupItem className="text-muted">
                        Saraivala sam brojnim ministarstvima Vlade Crne Gore, vladinim i nevladinim organizacijama, institucijama, agencijama i institutima, domaim i stranim udruenjima, meunarodnim donatorskim organizacijama (USAID, OEBS, IOM, Delegacija EU u Crnoj Gori, Svjetska banka, IFC, GTZ, agencije Ujedinjenih nacija u Crnoj Gori, Svjetska zdravstvena organizacija, Svjetska carinska organizacija, Svjetska trgovinska organizacija, itd.), meunarodnim i domaim bankama, lokalnim samoupravama, vie fakulteta Univerziteta Crne Gore, organima sudske vlasti, domaim i stranim konsultantskim kuama, domaim i stranim privatnim preduzeima, graevinskim firmama, advokatskim kancelarijama, agencijama za nekretnine, biltenima, prevodilakim agencijama, domaim i stranim fizikim licima, itd.
                        </MDBListGroupItem>
                      </MDBListGroup>
                    </MDBCol>
                  </MDBRow>
  
                  <MDBRow>
                    <MDBCol sm="12" md={{ size: 9, offset: 1 }}>
                      <MDBCardText className="text-muted">
                        Moj rad ukljuuje i projekte u oblasti evropskih integracija, u kojima sam uestvovala kao prevodilac i lanica radne grupe Ministarstva evropskih integracija Crne Gore za izradu prirunika za prevoenje pravne tekovine Evropske unije (Acquis communautaire).
                      </MDBCardText>
                    </MDBCol>
                  </MDBRow>
                </MDBCardBody>
              </MDBCard>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </section>
    );
  };
  