import {
    MDBCol,
    MDBContainer,
    MDBRow,
    MDBCard,
    MDBCardText,
    MDBCardBody,
    MDBIcon,
    MDBListGroup,
    MDBListGroupItem,
    MDBCardTitle
  } from 'mdb-react-ui-kit';
  import { Element } from 'react-scroll';
  import { Helmet } from 'react-helmet-async';

export const TranslationServicesPage = () => {
  
    return (
    <section style={{ backgroundColor: '#eee' }}>
      <Helmet>
        <title>Usluge prevoenja - Dragana Petronijevi</title>

        <meta name="description" content="Dragana Petronijevi - Usluge prevoenja strunih, ovjerenih i neovjerenih prevoda, simultanog i konsekutivnog prevoenja." />
        <meta name="keywords" content="dragana petronijevic, usluge prevoenja, prevodilac, sudski tuma, tuma, engleski jezik, crnogorski jezik, struni prevodi, ovjereni prevodi, neovjereni prevodi, simultano prevoenje, konsekutivno prevoenje" />
    </Helmet>
      <MDBContainer className="py-5">
        <MDBRow>
          <MDBCol lg="12">
                <MDBCard>
                    <MDBCardBody>
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardTitle className="fs-3">
                                        <b>Usluge prevoenja</b>
                                    </MDBCardTitle>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Prevodim i po potrebi ovjeravam sve vrste tekstova i dokumenata sa crnogorskog na engleski jezik i sa engleskog na crnogorski jezik, odnosno jezike u slubenoj upotrebi u Crnoj Gori. Takoe radim prevode knjiga, sajtova, aplikacija, audio i video materijala, lekturu i korekturu. Vrim prevode i ovjere zvaninih dokumenata. Po potrebi pruam usluge simultanog i konsekutivnog prevoenja.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <hr />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText><b>Oblasti prevoenja</b></MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <Element name="strucni-prevodi" />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Lini dokumenti, poslovna dokumentacija, prezentacije, broure, ugovori, pravna dokumentacija, sudski sporovi, ekonomija, raunovodsto, trgovina, medicina, farmacija, tehnika, elektrotehnika, industrija, poljoprivreda, energetika, prostorno planiranje, graevinarstvo, nauka, umjetnost, turizam, ugostiteljstvo, marketing, odnosi sa javnou, nekretnine i brojne druge oblasti.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <hr />
                        <MDBRow>
                                <MDBCol>
                                    <h4><b><MDBIcon fas icon="file-alt" /> Struni prevodi</b></h4>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Struni prevodi su prevodi tekstova namijenjenih itaocima unutar odreenog strunog podruja. Ovu vrstu dokumenata odlikuje visoka strunost za odreenu oblast, koju je potrebno da posjeduje prevodilac.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Struni prevodi koje radim su obino:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>	prevodi tekstova zakona, pravnih dokumenata (pravna oblast)</MDBListGroupItem>
                                        <MDBListGroupItem>	prevodi tehnikih materijala i uputstava za upotrebu (tehnika)</MDBListGroupItem>
                                        <MDBListGroupItem>	prevodi medicinskih nalaza (medicina)</MDBListGroupItem>
                                        <MDBListGroupItem>	prevodi tekstova sa podruja humanistikih, prirodnih, ekonomskih i kibernetskih nauka (nauni prevodi)</MDBListGroupItem>
                                        <MDBListGroupItem>	prevod Internet stranica (vrlo vani prevodi koji se moraju raditi sa posebnom panjom jer su namijenjeni irokom broju korisnika)</MDBListGroupItem>
                                        <MDBListGroupItem>	prevodi dokumentacije, poslovne korespondencije, prezentacija, reklamnih tekstova i slino</MDBListGroupItem>
                                        <MDBListGroupItem>	prevod reprezentativnih materijala za sajmove i izlobe</MDBListGroupItem>
                                        <MDBListGroupItem>	prevodi marketinkih tekstova</MDBListGroupItem>
                                        <Element name="ovjeren-prevod" />
                                        <MDBListGroupItem>	prevod poslovnih pisma</MDBListGroupItem>
                                        <MDBListGroupItem>	prevod prezentacija u PowerPoint, Excel, PDF formatu, itd.</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <hr />
                        <MDBRow>
                                <MDBCol>
                                    <h4><b><MDBIcon fas icon="stamp" /> Ovjeren prevod</b></h4>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Ovjeren prevod je prevod dokumenta koji je formalno verifikovan i moe se koristiti kao zvanian dokument. Prevod dokumenta ovjerava stalni sudski tuma potvrdom koja mora biti na jeziku na koji je dokument preveden. Ispod potvrde navodi se mjesto i datum, nakon ega slijedi svojeruni potpis i otisak peata stalnog sudskog tumaa.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Najei dokumenti koji zahtijevaju prevod sa ovjerom sudskog tumaa su:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>	sudski prevod svjedoanstva</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod diplome</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod izvoda iz matine knjige roenih</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod potvrde o nekanjavanju</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod policijskog zapisnika</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod potvrde o branom stanju</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod ljekarskog nalaza</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod ugovora</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod sertifikata</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod izvoda iz registra</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod ovlaenja</MDBListGroupItem>
                                        <Element name="neovjereni-prevod" />
                                        <MDBListGroupItem>	sudski prevod pasoa</MDBListGroupItem>
                                        <MDBListGroupItem>	sudski prevod punomoja, itd.</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <hr />
                        <MDBRow>
                                <MDBCol>
                                    <h4><b><MDBIcon fas icon="file" /> Neovjereni prevod</b></h4>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Neovjereni prevod je prevod koji ne sadri ovjeru ovlaenog sudskog tumaa. <br /> Neovjereni prevodi izdaju se u papirnom ili elektronskom formatu.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted"><b>Prevodim:</b></MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>	Prevod reprezentativnih materijala za sajmove i izlobe</MDBListGroupItem>
                                        <MDBListGroupItem>	prevod marketinkih tekstova</MDBListGroupItem>
                                        <MDBListGroupItem>	prevod poslovnih pisama</MDBListGroupItem>
                                        <Element name="simultano-prevodjenje" />
                                        <MDBListGroupItem>	prevod prezentacija</MDBListGroupItem>
                                        <MDBListGroupItem>	prevod dokumenata za slubene i privatne svrhe</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <hr />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted"><b>Po potrebi pruam usluge simultanog i konsekutivnog prevoenja.</b></MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <h4><b><MDBIcon fas icon="microphone-alt" /> Simultano prevoenje</b></h4>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Simultano prevoenje je vrsta usmenog prevoenja gdje prevodilac prevodi govor u realnom vremenu, dok govornik jo uvijek govori. Ovo se obino koristi na konferencijama, sastancima i drugim dogaajima gdje uestvuju osobe koje govore razliite jezike.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Primjeri gdje i kada se koristi simultano prevoenje:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>	Sastanci poslovnih odbora</MDBListGroupItem>
                                        <MDBListGroupItem>	Sudnice</MDBListGroupItem>
                                        <Element name="konsekutivno-prevodjenje" />
                                        <MDBListGroupItem>	Diplomatske konferencije</MDBListGroupItem>
                                        <MDBListGroupItem>	Meunarodne konferencije</MDBListGroupItem>
                                        <MDBListGroupItem>	Predavanja i prezentacije, itd.</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <h4><b><MDBIcon fas icon="comments" /> Konsekutivno prevoenje</b></h4>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Pojam konsekutivno prevoenje  definie se kao usmeno prevoenje odreenog sadraja na eljeni jezik, ali tako da se prevoenje vri tek kada govornik zavri odreeni segment izlaganja. Prevodilac paljivo prati izlaganje i po potrebi biljei kako bi mogao da na adekvatan nain prenese sadraj na odreeni jezik. </MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Primjeri gdje i kada se koristi konsekutivno prevoenje:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>	Manji sastanci ili skupovi</MDBListGroupItem>
                                        <MDBListGroupItem>	Sudnice</MDBListGroupItem>
                                        <MDBListGroupItem>	Konferencije za tampu</MDBListGroupItem>
                                        <MDBListGroupItem>	Obuke</MDBListGroupItem>
                                        <MDBListGroupItem>	Sastanci kod advokata, notara, itd.</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <h4><b><MDBIcon fas icon="question-circle" /> Razlika izmeu simultanog i konsekutivnog prevoenja</b></h4>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Simultano prevoenje je nain usmenog prevoenja u kojem govornik govori, a usmeni prevodilac istovremeno (ili simultano) reprodukuje njegov govor na jeziku koji njegovi sluaoci razumiju. Simultani prevodioci rade u kabinama, a umjesto toga mogu koristiti prenosnu opremu za simultano prevoenje (tzv. Infoport ili valiza) ili tehniku aptanog prevoenja.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Sve vjetine navedene za konsekutivno prevoenje odnose se i na simultano prevoenje.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Tri glavne radnje takoe su u osnovi jednake:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>1) aktivno sluanje (razumijevanje)</MDBListGroupItem>
                                        <MDBListGroupItem>2) analiziranje (strukturiranje poruke)</MDBListGroupItem>
                                        <MDBListGroupItem>3) reprodukovanje (prenoenje poruke).</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Razlika u odnosu na konsekutivno prevoenje je u tome to se kod simultanog prevoenja sve te radnje moraju odvijati istovremeno (ili simultano).</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                    </MDBCardBody>
                </MDBCard>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </section>
  );
};
