import {
    MDBCol,
    MDBContainer,
    MDBRow,
    MDBCard,
    MDBCardText,
    MDBCardBody,
    MDBIcon,
    MDBListGroup,
    MDBListGroupItem,
    MDBCardTitle
  } from 'mdb-react-ui-kit';
import { Helmet } from 'react-helmet-async';
import { NavLink } from 'react-router-dom';

export const PricePage = () => {
    return(
        <section style={{ backgroundColor: '#eee' }}>
            <Helmet>
                <title>Cjenovnik - Dragana Petronijevi</title>

                <meta name="description" content="Cjenovnik usluga prevoenja Dragane Petronijevi, sudskog tumaa za engleski jezik. Pronaite cijene za strune prevode, ovjerene i neovjerene prevode, simultano i konsekutivno prevoenje." />
                <meta name="keywords" content="dragana petronijevic, cjenovnik, cijene prevoenja, prevodilac, sudski tuma, ovjereni prevodi, neovjereni prevodi, simultano prevoenje, konsekutivno prevoenje, cjenovnik prevoda, sudski tuma" />
            </Helmet>
      <MDBContainer className="py-5">
        <MDBRow>
          <MDBCol lg="12">
                <MDBCard>
                    <MDBCardBody>
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardTitle className="fs-3">
                                        <b><MDBIcon fas icon="coins" /> Cjenovnik</b>
                                    </MDBCardTitle>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText><b>Nagrada za pismeno prevoenje</b></MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Za prevod pisanog teksta tumau pripada nagrada koja se obraunava po prevodilakoj strani. Jedna prevodilaka strana sadri 1800 slovnih znaka sa razmakom. <br /> Odreivanje cijene zavisi od sljedeih faktora:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                <MDBListGroup>
                                        <MDBListGroupItem>	oblast prevoenja</MDBListGroupItem>
                                        <MDBListGroupItem>	koliko prevedeni tekst ima prevodilakih strana</MDBListGroupItem>
                                        <MDBListGroupItem>	da li je za prevod potrebna ovjera sudskog tumaa</MDBListGroupItem>
                                        <MDBListGroupItem>	hitnost, odnosno u kom roku je potrebno zavriti prevod</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Za prevod:</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBListGroup>
                                        <MDBListGroupItem>	sa crnogorskog jezika ili drugog jezika koji je u slubenoj upotrebi u Crnoj Gori na engleski jezik.</MDBListGroupItem>
                                        <MDBListGroupItem>	sa engleskog jezika na crnogorski jezik ili drugi jezik koji je u slubenoj upotrebi u Crnoj Gori.</MDBListGroupItem>
                                    </MDBListGroup>
                                </MDBCol>
                        </MDBRow>
                        <hr />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText><b>Nagrada za usmeno prevoenje</b></MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                                <MDBCol>
                                    <MDBCardText className="text-muted">Nagrada za usmeno prevoenje koja pripada prevodiocu, odnosno tumau, obraunava se za svaki zapoeti sat usmenog prevoenja, konsekutivnog ili simultanog.</MDBCardText>
                                </MDBCol>
                        </MDBRow>
                        <MDBRow>
                            <MDBCol>
                                <MDBCardText className="text-muted">U vrijeme utroeno za prevoenje iz stava 1 ovog lana rauna se cjelokupno vrijeme od dolaska tumaa u dogovoreno vrijeme na mjesto pruanja usluga usmenog prevoenja do prestanka potreba za njegovim angaovanjem.</MDBCardText>
                            </MDBCol>
                        </MDBRow>
                        <MDBRow>
                            <MDBCol>
                                <MDBCardText className="text-muted">Tuma ima pravo na naknadu putnih trokova i drugih izdataka i naknadu izgubljene zarade u skladu sa propisom o naknadi trokova u sudskim postupcima.</MDBCardText>
                            </MDBCol>
                        </MDBRow>
                        <br />
                        <MDBRow>
                            <MDBCol>
                                <MDBCardText className="text-muted"><b>Za visinu nagrade u konkretnom sluaju, obratite se <NavLink to="/kontakt" className="text-decoration-underline">telefonom ili putem e-pote.</NavLink></b></MDBCardText>
                            </MDBCol>
                        </MDBRow>
                    </MDBCardBody>
                </MDBCard>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
      </section>
    );
}